@import "index.scss";
@font-face {
  font-family: "oswald-light";
  src: url("./resources/fonts/Oswald-Light.ttf");
}

@font-face {
  font-family: "oswald-extra-light";
  src: url("./resources/fonts/Oswald-ExtraLight.ttf");
}

@font-face {
  font-family: "Roboto";
  src: url("./resources/fonts/Roboto-Regular.ttf");
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: var(--secondary-color-lighter);
  line-height: 22px;

  -webkit-font-smoothing: antialiased;
  background: var(--primary-color);
  margin: 0;
}

h1,
h2,
h3 {
  margin-top: 16px;
  font-family: oswald-extra-light, sans-serif;
  border-bottom: 0px solid #dedede;
  padding-bottom: 3px;
}

.page-layout {
  margin-top:70px;
}

#bottom-menu {
  position: fixed;
  bottom: -30px;
  width: 100%;
  height: auto;
  background: var(--secondary-color-darker);
  -webkit-transition: bottom 1s;
  transition: bottom 1s ease-in-out;
  text-align: center;
}
